import { useState } from "react";
import { Formik } from 'formik';

function ProductPopup(props) {
  const { card } = props;
  const [isRadiusValue, setIsRadiusValue] = useState(card.prices ? card.prices[0].radius : null);
  function checkedRadius(event) {
    setIsRadiusValue(event.target.value)
  }

  //счетчик
  const [isСounter, setIsСounter] = useState(1);

  function handleClickMinus() {
    if (isСounter > 1) {
      setIsСounter(isСounter - 1)
    } else {
      setIsСounter(1)
    }
  }

  function handleClickAdd() {
    setIsСounter(isСounter + 1)
  }


  return (
    <Formik
      initialValues={{
        id: card.id,
        name: '',
        counter: isСounter,
        radius: {isRadiusValue},
      }}
    >
      {({ values, errors, touched, handleChange, handleBlur, isValid, handleSubmit, dirty }) => (
        <form className="popup__form">
          <button className="popup__button_close">
            <span className="popup__span popup__span-first"></span>
            <span className="popup__span popup__span-second"></span>
          </button>
          <img src={card.img} className="popup__img" alt={card.name} />
          <h3 className="popup__name">{card.name}</h3>

          <div className="popup__radius-items">
            {card.prices ?
              card.prices.map((price) =>
                <div className="popup__radius-item" key={price.id}>
                  <input
                    type="radio"
                    className="popup__radius-input"
                    id={price.id}
                    name="radius"
                    value={price.radius}
                    onClick={checkedRadius}
                  ></input>
                  <label
                    htmlFor={price.id}
                    className="popup__radius-label"
                  >
                    {price.radius}
                  </label>
                </div>
              )
              : null
            }
          </div>

          {isСounter > 0 && <span>{isСounter}</span>}

          <p>{card.composition}</p>

          <div className='popup__buttons'>
            <button className='popup__button popup__button_minus' onClick={handleClickMinus}>-</button>
            <button className='popup__button popup__button_plus' onClick={handleClickAdd}>+</button>
          </div>

          <button>
            Добавить в корзину
          </button>
        </form>
      )}


    </Formik>
  )
}

export default ProductPopup;