import Card from '../Card/Card';

function Products(props) {
  return (
    <section className="products">
      {props.category.map((card, index) =>
        <Card
          key={index}
          card={card}
          name={card.name}
          price={card.price}
          prices={card.prices}
          discount={card.discount}
          img={card.img}
          composition={card.composition}
        />
      )}
    </section>
  )
}

export default Products;