import { useState } from 'react';
import ReactDOM from 'react-dom';
import classNames from 'classnames'

import ProductPopup from '../ProductPopup/ProductPopup';

function Card(props) {
  const [isActiveProductPopup, setIsActiveProductPopup] = useState(false);
  function handleClickCard() {
    setIsActiveProductPopup(true)
    window.scrollTo(0, 0);
    const div = document.getElementById('product-popap');
    const div2 = document.querySelector(`.card__${props.card.id}`);
    document.addEventListener('click', (event) => {
      const withinBoundaries = event.composedPath().includes(div);
      const withinBoundaries2 = event.composedPath().includes(div2);

      if (!withinBoundaries && !withinBoundaries2) {
        setIsActiveProductPopup(false)
      }
    })
  }

  return (
    <article className={`card__${props.card.id}`} onClick={handleClickCard}>
      {isActiveProductPopup &&
        ReactDOM.createPortal(
          <div className='product-info__form product-info__form-review_active' >
            <ProductPopup
              card={props.card}
            />
          </div>,
          document.getElementById('product-popap')
        )
      }

      <img src={props.img} className="card__img" alt={props.name} />
      <h3 className="card__name">{props.name}</h3>

      {props.price ?
        <span className={classNames('card__price',
          { 'cards__price_line': props.discount })}>{props.price} руб.</span>
        :
        <span className={classNames('card__price',
          { 'cards__price_line': props.prices[0].discount })}>{props.prices[0].price} руб.</span>
      }

      {props.discount ?
        <span className="card__price_discount">{props.discount} руб.</span> :
        props.discount === null || props.prices[0].discount === null ?
          null :
          <span className="card__price_discount">{props.prices[0].discount} руб.</span>
      }

      <div className="card__buttons">
        <button className="card__button" onClick={handleClickCard}>В корзину</button>
      </div>

    </article>
  )
}

export default Card;

