function Footer() {
  function handleClickCart() {
    console.log('cart');
  }

  function handleClickMenu() {
    console.log('menu');
  }

  function handleClickStock() {
    console.log('stock');
  }

  return (
    <header className="footer">
      <nav className="footer__nav">

        <button className="footer__cart" onClick={handleClickCart}>Д</button>

        <div className="footer__buttons">
          <button className="footer__button" onClick={handleClickMenu}>Меню</button>
          <button className="footer__button" onClick={handleClickStock}>Акции</button>
        </div>

      </nav>
    </header>
  )
}

export default Footer;